'use client';

import { useState, useEffect } from 'react';

interface Satellite {
  id: number;
  name: string;
  status: 'Nominal' | 'Degraded' | 'Critical';
  signal: number;
  latency: number;
}

export default function Dashboard() {
  const [satellites, setSatellites] = useState<Satellite[]>([
    { id: 1, name: 'SAT-001', status: 'Nominal', signal: 95, latency: 120 },
    { id: 2, name: 'SAT-002', status: 'Nominal', signal: 92, latency: 125 },
    { id: 3, name: 'SAT-003', status: 'Degraded', signal: 65, latency: 250 },
    { id: 4, name: 'SAT-004', status: 'Nominal', signal: 88, latency: 130 },
    { id: 5, name: 'SAT-005', status: 'Nominal', signal: 91, latency: 115 },
    { id: 6, name: 'SAT-006', status: 'Critical', signal: 20, latency: 500 },
  ]);

  const [phase, setPhase] = useState(73);
  const [task, setTask] = useState('Orbital Adjustment');

  useEffect(() => {
    const timer = setInterval(() => {
      setPhase((prev: number) => (prev >= 100 ? 0 : prev + 2));
      setTask((prev: string) => 
        prev === 'Orbital Adjustment' ? 'Station Keeping' : 'Orbital Adjustment'
      );
      
      setSatellites((prev: Satellite[]) =>
        prev.map((sat: Satellite) => ({
          ...sat,
          signal: Math.max(10, Math.min(99, sat.signal + (Math.random() - 0.5) * 15)),
          latency: Math.max(100, Math.min(600, sat.latency + (Math.random() - 0.5) * 50)),
        }))
      );
    }, 3000);
    return () => clearInterval(timer);
  }, []);

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'Nominal':
        return 'text-green-400';
      case 'Degraded':
        return 'text-yellow-400';
      case 'Critical':
        return 'text-red-400';
      default:
        return 'text-gray-400';
    }
  };

  return (
    <div className="min-h-screen bg-black text-white p-6">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-5xl font-bold mb-2 text-cyan-400">Mission Control</h1>
          <p className="text-gray-400">AstraGuard AI Satellite Operations Dashboard</p>
        </div>

        {/* Satellite Grid */}
        <div className="mb-8">
          <h2 className="text-2xl font-bold mb-4 text-cyan-300">Active Satellites</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-6 gap-4">
            {satellites.map((sat: Satellite) => (
              <div
                key={sat.id}
                className="bg-slate-900 border border-cyan-500 rounded p-4 hover:bg-slate-800 transition hover:border-cyan-300 glow-cyan"
              >
                <div className="font-bold text-cyan-300 mb-2">{sat.name}</div>
                <div className="text-sm mb-3">
                  <div className="flex justify-between mb-2">
                    <span>Status:</span>
                    <span className={getStatusColor(sat.status)}>{sat.status}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Signal:</span>
                    <span className="text-cyan-400">{Math.round(sat.signal)}%</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Latency:</span>
                    <span className="text-cyan-400">{Math.round(sat.latency)}ms</span>
                  </div>
                </div>
                <div className="w-full bg-slate-700 h-2 rounded overflow-hidden">
                  <div
                    className="bg-cyan-500 h-full rounded transition-all"
                    style={{ width: `${sat.signal}%` }}
                  ></div>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Mission Status */}
        <div className="bg-slate-900 border border-cyan-500 rounded p-6 glow-cyan">
          <h2 className="text-2xl font-bold mb-4 text-cyan-300">Mission Status</h2>
          
          <div className="mb-6">
            <div className="flex justify-between mb-2">
              <span>Stable Orbit Phase</span>
              <span className="text-cyan-400 font-bold">{phase}%</span>
            </div>
            <div className="w-full bg-slate-700 h-4 rounded overflow-hidden">
              <div
                className="bg-gradient-to-r from-cyan-500 to-blue-500 h-full transition-all"
                style={{ width: `${phase}%` }}
              ></div>
            </div>
          </div>

          <div className="bg-slate-800 rounded p-4 border border-slate-700">
            <div className="text-sm text-gray-400 mb-2">Current Task</div>
            <div className="text-xl font-bold text-cyan-300">{task}</div>
            <div className="text-sm text-gray-400 mt-2">
              ETA: {phase >= 90 ? '15 minutes' : phase >= 50 ? '45 minutes' : '2 hours'}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
